.loginContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
    background-image: url('../../public/bgdots.png');
    background-color: #f9f9f9;
}

.loginContainer h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.loginContainer h1{
    font-family: "GeneralSans B";
    font-size: 60px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -2px;
    margin-bottom: 0px;
}

.loginContainer p {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 2rem;
}

.loginContainer a {
    color: #F96D31;
    text-decoration: none;
}

.inputContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
    position: relative;
    width: 100%;
    max-width: 400px;
    
}

.inputContainer input {
    width: 100%;
    padding: 0.75rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 6rem;
}

.loginContainer button {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    color: #fff;
    background-color: #000;
    border: none;
    border-radius: 6rem;
    cursor: pointer;
}

.loginContainer button:hover {
    background-color: #333;
}