.container{
    max-width: 1283px;
    margin: 0 auto;
    padding-top: 150px;
    padding-bottom: 50px;
    /* background-color: #F8F8F8; */
}

.border{
    height: 68px;
    border-radius: var(--Radius_lg_1, 20px) var(--Radius_lg_1, 20px) 0px 0px;
    background: #000; 

    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 40px;
}

.content{
    padding: 50px;
    background-color: #F8F8F8;
}

.myname{
    color: #000;
font-feature-settings: 'clig' off, 'liga' off;
font-family: "General Sans";
font-size: 50px;
font-style: normal;
font-weight: 600;
line-height: normal;
text-transform: capitalize;
}

.header{
    display: flex;
    flex-direction: row;
    gap: 40px;
    align-items: center;
}

.header h4{
    font-family: "General Sans";
font-size: 40px;
font-style: normal;
font-weight: 600;
margin-top: 0px;
margin-bottom: 0px;
}

.plus{
    width: 55.173px;
height: 46.667px;
padding: 4.403px;
text-align: center;
align-items: center;
    border-radius: 6.605px;
    border: 4.403px solid #000;
    background: #F2CE0F;
    box-shadow: 7px 7px 0px 0px #000;

    color: #000;

font-feature-settings: 'clig' off, 'liga' off;
font-family: "General Sans";
font-size: 30px;
font-style: normal;
font-weight: 700;
line-height: normal;
}