.container{
    background-image: url('../../public/bgdots.png');
    background-color: #fff;

}

.bodyText a {
    color: #F98317;
}

.wrapper{
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    gap: 50px;
    padding-top: 150px;
    padding-bottom: 100px;
}

.card{
    background-image: url('../../public/mgimage.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-origin: padding-box;
    
    /* height: 700px; */
    width: 50%;
    background-color: #DADADA;
    border-radius: 20px;
    height: 700px;
}

.screenshotContainer{
    display: flex;
    justify-content: center;
    margin-top: 90px;
}

.screenshot{
    width: 55%;
    box-shadow: 0px 3.648px 170px 0px rgba(0, 0, 0, 0.10);
}


.border{
    height: 68px;
    border-radius: var(--Radius_lg_1, 20px) var(--Radius_lg_1, 20px) 0px 0px;
    background: #727272; 

    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 40px;
    
}


/* Section 2 */
.content{
    flex-grow: 1;
    width: 50%;
}

.headerText{
    font-family: "GeneralSans M";
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.subText{
    color: var(--text-color);
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal; 
}

.bodyText{
    color: var(--text-color);
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}


/*  */
.description{
    color: var(--text-color);
    max-width: 95%;
    margin: 0 auto;
    font-size: 25px;
font-style: normal;
font-weight: 600;
}

.boldColorText{
    color: #F2620F;
    font-family: "GeneralSans B";
}

.quote{
    max-width: 70%;
    margin: 0 auto;
    padding: 60px;
}

.footer{
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 150px;
}


.btn {
    color: white;
    width: 387px;
    height: 80px;
    border-radius: 9.908px;
    border: 6.605px solid #000;

    background: #F2620F;

    font-size: 35px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    box-shadow: 10px 13px 0px 0px #000;
    cursor: pointer;
}





/* Breakpoints for tablet */

@media (max-width: 768px) {
    .headerText{
        font-size: 25px;
    }
    .subText{
        font-size: 18px;
    }
    .bodyText{
        font-size: 18px;
    }
    .description{
        font-size: 18px;
    }
    .marygoldDate{
        width: 40%;
    }

    .card {
        border-radius: 15px;
        height: 700px;
    }

    .screenshot {
        /* width: 80%; */
        box-shadow: 0px 3.648px 170px 0px rgba(0, 0, 0, 0.10);
    }


    .border {
        height: 40px;
        border-radius: var(--Radius_lg_1, 15px) var(--Radius_lg_1, 15px) 0px 0px;

    }
    .image{
        width: 20%;
    }
  
}


/* Breakpoints for mobile */
@media (max-width: 480px) {

    .wrapper {
        max-width: 90%;
        flex-direction: column;
        align-items: center;
        padding-bottom: 20px;
    }

    .card{
        width: 100%;
        height: 700px;
    }
    .content{
        width: 100%;
    }
    .description{
        max-width: 90%;
    }
    .footer {
        max-width: 60%;
        gap: 40px;
        flex-direction: column;
    }
    .footer img{
        width: 40%;
    }
    .btn {
        width: 300px;
        font-size: 30px;
    }

}