@import './fonts.css';

*{
  scroll-behavior: smooth;
}
body {
  margin: 0 auto;
  font-family: "GeneralSans R", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
background-color: var(--bg-color);

  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: #F15600; 
  --text-color: #65615F; 
  --bg-color: #FCF3EE; 
  --white: #ffffff; 
}
