
.navbar{
    position: -webkit-sticky; 
    position: sticky;
    top: 0;
    background-color: black;
    justify-content: center;
    z-index: 1000;
    margin-top: 30px;
    /* max-width: 1283px; */
    width: 100% !important;
    margin: 0 auto;
    
}

.navList {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

.navItem {
    flex-grow: 1;
    text-align: center;
    padding: 23px 0px;
    border-left: 1px solid rgba(255, 255, 255, 0.50);
    cursor: pointer;
}
  

.navItem a{
    text-decoration: none;
    color: #B3B3B3;
}


.navItemLink.active{
  background-color: var(--primary-color);
  color: white;
}

/* Styling Li element when LINK is active */
.navItemSelected{
    background-color: var(--primary-color);

}






/* Breakpoints for tablet */

@media (max-width: 768px) {
    .navbar {
        /* justify-content: flex-start; */
        /* Align items to the start */
    }
}


/* Breakpoints for mobile */
@media (max-width: 480px) {

    .navItem {
        padding: 18px 0px;
    }

    .navItem a{
        font-size: 15px;
    }

}