.container {
    /* background-image: url('../../public/bgdots.png'); */
    background-color: #F9F9F9;
}

.summaryContainer {
    display: flex;
    gap: 60px;
}

.appiconhomescreen{
    width: 350px;
}

.iconText{
    display: flex;
    gap: 30px;
}
.boldText {
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    font-family: "GeneralSans M";
}

strong{

        font-size: 25px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
}

.teamRoleContainer {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.timelineToolsContainer {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.topic {
    color: #48C25D;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: "GeneralSans M";
    font-size: 50px;
    font-style: normal;
    font-weight: 600;
}

.columnContainer {
    display: flex;
    gap: 60px;
}

.smallHeaderText {
    color: #000;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: "GeneralSans M";
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    margin: 0;
}

.smallText {
    color: #666;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: "GeneralSans R";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

/* SECTION 2 */
.section2 {
    background-color: white;
    padding: 30px 50px 50px 50px;
    border-radius: 20px;
}

.subHeaderText {
    color: #000;
    font-family: "GeneralSans M";
    font-size: 50px;
    font-style: normal;
    font-weight: 500;
}

/* SECTION 2 */


/* Break Point */
.hLine {
    height: 1px;
    border: 0;
    border-top: 1px solid #48C25D;
    margin: 1em 0;
    padding: 0;
    margin-top: 100px;
    margin-bottom: 100px;
}

/* Break Point */


/* Funny Text */
.funnyFontContainer {
    display: flex;
    gap: 30px;
}

.funnyFont {
    font-family: "Gochi Hand", cursive;
    font-weight: 400;
    font-style: normal;
    font-size: 50px;
    color: #000;
}

/* Funny Text */


/* Pain Points */
.painPoints {
    display: flex;
    gap: 50px;
}

.painpointContainer {
    background-color: white;
    border-radius: var(--Radius_lg_1, 20px);
    padding: 20px var(--Radius_lg_1, 20px);
}

.painpoint {
    color: #000;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: "Gochi Hand";
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration-line: underline;
}

/* Pain Points */



/* ****************** FORMATTING ****************** */

/* FORMAT IMAGE */
.centerImage {
    display: flex;
    margin: 0 auto;
    width: 80%;
    margin-top: 100px;
    margin-bottom: 100px;
}

.appicon{
    margin: 0 auto;
    display: flex;
}

/* FORMAT IMAGE */

/* TEXT FORMATTING */
/* Sub Text */
.subText {
    color: var(--text-color);
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    font-family: "GeneralSans M";
}

/* Sub Text */

.emphasis {
    font-size: 25px;
    font-weight: 600;
    color: #F2620F;
    font-family: "GeneralSans M";
}

/* TEXT FORMATTING */

/* SECTION 3 */
.numbers {
    display: flex;
    gap: 50px;
}

.numbersContent {
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.whiteSection {
    background-color: white;
    padding: 30px 50px 50px 50px;
    border-radius: 20px;

}


/* SECTION 7 */
.section7 {
    padding: 50px 80px 80px 80px;
    background-color: #48C25D;
    border-radius: 20px;
    margin-top: 100px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;

}

.numbers{
    text-align: center;
    font-family: Coolvetica;
    font-size: 100px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    color: #000;
    margin-bottom: 20px;
}

.numberText{
    margin-bottom: 50px;
    width:70%;
}

.screenshot{
    width: 35%;
}

.whiteText {
    color: white;
}
.quote{

}


.wrapper {
    max-width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    gap: 50px;
    padding-top: 100px;
    padding-bottom: 100px;
    align-items: center;
}

.card {
    background-image: url('../../public/tap2sendScreenshot.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-origin: padding-box;
    height: 700px;
    width: 50%;
    background-color: #DADADA;
    border-radius: 20px;
    /* padding-top: 20px; */

    overflow: hidden;
}


.border {
    height: 68px;
    border-radius: var(--Radius_lg_1, 20px) var(--Radius_lg_1, 20px) 0px 0px;
    background: #727272;

    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 40px;

}


/* Section 2 */
.content {
    flex-grow: 1;
    width: 50%;
}



.headerText {
    font-family: "GeneralSans M";
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    color: #48C25D;
}



.bodyText {
    color: var(--text-color);
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.bold {
    color: #666;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: "General Sans";
    /* font-size: 20px; */
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}


/*  */
.description {
    color: var(--text-color);
    max-width: 90%;
    margin: 0 auto;
    font-size: 25px;
    font-style: normal;
    font-weight: 600;
}

.boldColorText {
    color: #F2620F;
    font-family: "GeneralSans B";
}

.quote {
    max-width: 900px;
    margin: 0 auto;
    padding: 60px;
}

.footer {
    max-width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 150px;
}

.footer button {
    color: white;
    width: 387px;
    height: 80px;
    border-radius: 9.908px;
    border: 6.605px solid #000;

    background: #F2620F;

    font-size: 35px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    box-shadow: 10px 13px 0px 0px #000;

}








/* Breakpoints for tablet */

@media (max-width: 768px) {
    .headerText {
        font-size: 25px;
    }

    .summaryContainer p {
        font-size: 15px;
    }

    .card {
        height: 500px;
        border-radius: 10px;
    }

    .teamRoleContainer {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    .timelineToolsContainer {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    /* FONTS */

    .boldText {
        font-size: 20px;
    }

    .topic {
        font-size: 35px;
        margin-top: 0px;
    }

    .smallText {
        font-size: 18px;
    }

    .subHeaderText {
        font-size: 30px;
    }

    p {
        font-size: 20px;
    }

    .funnyFont {
        font-family: "Gochi Hand", cursive;
        font-size: 35px;
    }

    /* FONTS END */

    .painPoints {
        flex-direction: column;
    }

    .numbers {
        flex-direction: column;
    }

    .section7 {
        padding: 30px;
    }

    .whiteSection {
        padding: 30px;
    }

}


/* Breakpoints for mobile */
@media (max-width: 480px) {
    .wrapper {
        flex-direction: column;
    }

    .card {
        width: 100%;
    }

    .content {
        width: 100%;
    }

    .columnContainer {
        flex-direction: column;
        gap: 10px;
    }





}