.container{
    background-image: url('../../public/bgdots.png');
    background-color: #FF8D41;
}

.wrapper{
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    gap: 50px;
    padding-top: 150px;
    padding-bottom: 100px;
    
}

.cardContainer{
    width: 50%;
    text-align: center;
}

.card{
    background-image: url('../../public/newwebsite.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-origin: padding-box;

    /* height: 700px; */
    /* width: 50%; */
    background-color: #FFCBA8;
    border-radius: 20px;
    height: 600px;
}

/* .screenshotContainer img{
    display: flex;
    justify-content: center;
    margin-top: 50px;
} */
.screenshotContainer{
    display: flex;
    justify-content: center;
    margin-top: 50px;
}



.screenshot{
    width: 80%;
    box-shadow: 0px 3.648px 170px 0px rgba(0, 0, 0, 0.10);
}


.oldwebsite{
    width: 90%;
}


.border{
    height: 68px;
    border-radius: var(--Radius_lg_1, 20px) var(--Radius_lg_1, 20px) 0px 0px;
    background: #727272; 

    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 40px;
    
}


/* Section 2 */
.content{
    flex-grow: 1;
    width: 50%;
}

.headerText{
    font-family: "GeneralSans M";
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.subText{
    color: var(--white);
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal; 
}

.bodyText{
    color: var(--white);
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}


/*  */
.description{
    color: var(--white);
    max-width: 95%;
    margin: 0 auto;
    font-size: 25px;
font-style: normal;
font-weight: 600;
}

.description a{
    color: black;
}

.boldColorText{
    color: #000;
    font-family: "GeneralSans B";
}

.quote{
    max-width: 70%;
    margin: 0 auto;
    padding: 60px;
    text-align: center;
}

.footer{
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 150px;
}

.footer button{
    color: black;
    width: 387px;
    height: 80px;
    border-radius: 9.908px;
    border: 6.605px solid #000;

    background: #F2CE0F;

    font-size: 35px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    box-shadow: 10px 13px 0px 0px #000;

}



/* Breakpoints for tablet */

@media (max-width: 768px) {
    .headerText {
        font-size: 25px;
    }

    .subText {
        font-size: 18px;
    }

    .bodyText {
        font-size: 18px;
    }

    .description {
        font-size: 18px;
    }

    .marygoldDate {
        width: 40%;
    }

    .card {
        border-radius: 15px;
        height: 350px;
    }

    .screenshot {
        /* width: 80%; */
        box-shadow: 0px 3.648px 170px 0px rgba(0, 0, 0, 0.10);
    }


    .border {
        height: 40px;
        border-radius: var(--Radius_lg_1, 15px) var(--Radius_lg_1, 15px) 0px 0px;

    }

    .image {
        width: 20%;
    }

}


/* Breakpoints for mobile */
@media (max-width: 480px) {

    .wrapper {
        max-width: 90%;
        flex-direction: column;
        align-items: center;
        padding-bottom: 20px;
    }

    .card {
        width: 100%;
        height: 350px;
    }

    .content {
        width: 100%;
    }

    .description {
        max-width: 90%;
    }

    .footer {
        max-width: 60%;
        gap: 40px;
        flex-direction: column;
    }

    .footer img {
        width: 40%;
    }

    .footer button{
        width: 300px;
        font-size: 30px;
    }

    .content {
        flex-grow: 1;
        width: 100%;
    }
    .cardContainer{
        width: 100%;
    }

}