/* src/fonts.css */
@font-face {
    font-family: 'GeneralSans M';
    src: url('fonts/GeneralSans-Medium.woff2') format('woff2');
         /* url('./assets/fonts/MyFont-Regular.woff') format('woff'); */
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'GeneralSans B';
    src: url('fonts/GeneralSans-Semibold.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'GeneralSans R';
    src: url('fonts/GeneralSans-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
  }
  
  /* You can add more @font-face rules if needed */
  