.container{
    width: 100%;
    /* height: 100vh; */
    display: flex;
    flex-direction: column;
    background-image: url('../../public/bgdots.png');
    background-color: var(--bg-color);

}

.wrapper{
    display: flex;
    flex-direction: column;
    max-width: 60%;
    /* height: 100%; */
    margin: 0 auto;
    padding: 70px 0;
   
}

.headerText{
    font-family: "GeneralSans B";
    font-size: 80px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -2px;
    margin-bottom: 0px;
    
}

.boldOrangeHeaderText{
    color: var(--primary-color);
    
}

.subText{
    font-size: 80px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-top: 0px;
    margin-bottom: 20px;
    font-family: "Gochi Hand";


}

.bodyText{
    font-size: 25px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    color: var(--text-color);
    margin-top: 0px;
}

.bodyText b{
    font-family: "GeneralSans B";
}

.company1 a{
    color: #F98317;
}

.company2 a{
    color: #65C22C;
}

.project1 a{
    color: #1AC4DB;
}

.fontType a{
    color: #DD7CFF;
}



/* Icon Assets */

.figmaIconContainer{
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    width: 80%;
    /* margin-top: 50px; */
    /* margin-right: 50px; */
    
}
.figmaIcon{
    width: 150px;
}





/* Breakpoints for tablet */

@media (max-width: 768px) {
    .wrapper {
        max-width: 80%;
        padding: 30px 0;
    }
    .headerText{
        font-size: 60px;
    }
    .subText{
        font-size: 60px;
    }
    .bodyText{
        font-size: 20px;
    }
}


/* Breakpoints for mobile */
@media (max-width: 480px) {
    .headerText {
            font-size: 50px;
    }

    .subText {
        font-size: 45px;
    }

    .bodyText {
        font-size: 20px;
    }
    .figmaIcon{
        width: 100px;
    }

}